.footer{
    display: flex;
    align-items: center;
    position:relative; 
    margin: -0.5rem  -3.5rem;
    margin-top:-7rem ;
}

.f--contents{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  width: 100%;
  margin-top: 20rem;
  text-align: center;
  gap:0.5rem;

}

.footer--icons{
    display: flex;
    justify-content: center;
    color: white;
    gap:1rem;
}
.footer--icons>*{
    cursor: pointer;

}

.footer--icons>*:hover{
    color: var(--orange);
    cursor: pointer;

}

.f--contents>span{
    
    font-size: 12px;
    color: white;
}



