.portfolio{
   
    align-items: center;
    justify-content: center;
    height: 70vh;
    padding: 0 3rem;
    margin-bottom: 8rem;
}

.p--title{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5rem;
}

.p--title>:nth-child(1) {
    color:var(--black);
    font-size: 2rem;
    font-weight: bold;
}
.p--title>:nth-child(2) {
    color:var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}

.portfolio-slider{
    width: 80%;
}
.portfolio .swiper{
    overflow: visible !important;
}

.portfolio-slider{
    margin-top:3rem;
    width: 100%;
}

.portfolio-slider .swiper-slider {
    width: 20rem;
}

.portfolio img {
    width: 21.5rem;
    border-radius: 20px;
    filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
}