.navbar{
    padding: 0 4rem;
}
.nav--wrapper {
    height: 10vh;
    display: flex;
    justify-content: space-between;    
    
}
.nav--left{
    flex: 1;
    display: flex;
    align-items: center;
    gap: 2rem;
    
}

.nav--name{
    font-size: 3rem;
    font-weight: bolder;
}

.nav--right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.nav--list{
    flex: 10;
    
}
.nav--list>ul{
    display: flex;
    gap:2rem;
    margin-right:4rem;
    list-style-type:none;

}
.nav--list >ul>:hover{
    cursor: pointer;
    color: var(--orange);  
}

.btn--uni{
    width: 6rem;
    height: 2.5rem;
}

