:root {
  --yellow: #f5c32c;
  --orange: #0096FF;
  --card:#66b3ff;
  --black: #242d49;
  --gray: #788097;
  --blueCard: #ddf8fe;
  --purple: rgb(238 210 255);
  --boxShadow: 0px 19px 60px rgb(0 0 0/8%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);


}

.App {
  
    
    overflow: hidden;
    color: var(--black);
    
}
html, body {
  
  max-width: 100%;
  overflow-x: hidden;
}


.btn--gen{
  background-color: var(--orange);
  box-shadow: none;
  border-radius: 15px;
  padding: 8px 20px;
  border: none;
  color:white;
  cursor: pointer;
}

.btn--gen:hover{
  border: 2px solid var(--gray);
  background-color: white;
  color: var(--gray);
}

.blur--pink{
  background: rgb(238 210 255);
  position:absolute;
  width: 22rem;
  height: 18rem;
  border-radius:50%;
  filter:blur(78px);
  z-index: -9;
  top:18%;
  left:68%;
}

.blur--blue{
  background: #c1f5ff;
  top: 17rem;
  width: 22rem;
  height: 18rem;
  filter:blur(78px);
  left: -5rem;
  z-index: -9;
 
  
}
