.experience{

 display: flex;
 align-items: center;
 justify-content: center;
 position:relative;
 gap:8rem;
 margin-top: none;
 height: 20vh;
 margin-top: 2rem;

}
.achievement{
        display: flex;
        flex-direction: column;
        align-items: center;
}
.circle{
    width: 4rem;
    height: 4rem;
    background-color: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
}

.circle::before{
    border-radius: 100%;
    content:'';
    background-image: linear-gradient(to bottom, #87e6fb, rgb(244, 166, 39));
    top: -8px;
    left:-8px;
    bottom: -8px;
    right: -8px;
    position:absolute;
    z-index: -1;
    box-shadow: var(--boxShadow);
}

.achievement>:nth-child(3)
{
    color: var(--orange);
}
