.con--wrapper{
    display: flex;
    position: relative;
    flex-direction: column;
    height: 70vh;
  
}

.con--left {
    display: flex;
    flex:1;
    flex-direction: column;
    position:absolute;
    justify-content: center;
    align-items: center;

}

.con--left>:nth-child(1) {
    font-size: 3rem;
    font-weight: bold;
    color: var(--black);
    align-items: center;
    justify-content: center;
    margin-left: 5rem;
    margin-top:2rem;

}

.con--left>:nth-child(2) {
    font-size: 3rem;
    font-weight: bold;
    color: var(--orange);
    align-items: center;
    justify-content: center;
    margin-left: 5rem;
    

}

.con--right{
    display: flex;
    flex:1;
    flex-direction: column;
    justify-content:center ;
    position: absolute;
    align-items: center;
    position: relative;
    gap:2rem;
    top:-1.5rem;
    left:12rem;
    width:100%;
}

input {
    width: 20rem;
    height: 2rem;
    border-radius: 6px;
    border:solid 2px var(--orange);
    
}
textarea {
    width: 20rem;
    height: 5rem;
    border-radius: 6px;
    border:solid 2px var(--orange);
    
}