.t--wrapper{
    padding: 0 3rem 0 3rem;
    height:100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top:7rem;
    flex-direction: column;
    gap: 2rem;
    position: relative;
}

.t--heading{
    align-self: start;

}
.t--heading>*{
    font-size: 2rem;
    font-weight: bold;
}
.t--heading>:nth-child(2) {
    color: var(--orange)
}

.testimonial{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    text-align: justify;
    padding: 2rem;
    width: 30rem;
    height: 12rem;
    background: rgba(255, 255, 255, 0.26);
    border:7px solid var(--blueCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;

}

.testimonial>img{
    width: 6rem;
    height: 6rem;
    border-radius: 100%;
}
.testimonial>:nth-child(2){
    color: var(--gray);
}

.t--wrapper .swiper {
    width:100%;
    height:70%;
}

.t--wrapper .swiper-pagination-bullet-active{
    background: var(--orange);
}

.t--wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}
.t--wrapper

.back--pink{
    background: rgb(238 210 255);
    position: absolute;
    left:38rem;
    width: 30rem;
    height: 18rem;
    border-radius:50%;
    filter:blur(78px);
    z-index: -9;
    background-repeat: no-repeat;
  
  }
  
  .back--blue{
    position: absolute;
    background: #c1f5ff;
    width: 30rem;
    height:18rem;
    left:1rem;
    filter:blur(78px);
    z-index: -9;
    background-repeat: no-repeat;
    
  }