.card{
    width: 12rem;
    height: 14rem;
    border-radius: 15px ;
    position: relative;
    display:flex;
    flex-direction: column;
    border:6px solid var(--card);
    align-items: center;
    gap:0.5rem
   
}

.card>:nth-child(1)
{   
    display: flex;
    position: relative;
    color: var(--black);
    margin-top:9rem;
   
}

.btn--card{
    width: 6rem;
    height: 2rem;
    font-size: 14px;
    background: var(--card);
    box-shadow: none;
    border-radius: 15px;
    padding: 8px 20px;
    border: none;
    color:white;
    cursor: pointer;
    
}
.btn--card:hover{
    border: 2px solid var(--gray);
    background-color: white;
    color: var(--gray);
  }