
.services{
    display: flex;
    height:90vh;
    margin-bottom: 10rem;
    margin-top:5rem;
    padding: 0 3rem 0 3rem;
    
    
}

.s--left{
    display: flex;
    flex:1;
    flex-direction: column;
    position:relative;
    gap:2rem;
    margin-right:10rem;
    
}

.s--left>:nth-child(1)
{
    font-size: 3rem;
    font-weight: bold;
    color: var(--black)
}
.s--left>:nth-child(2)
{
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--orange);
}
.s--left>:nth-child(3) 
{
    font-size: 15px;
    color:var(--gray);
    font-family: sans-serif;
    text-align: justify;
    padding-right: 20px;
    

}


.btn--cv {
    width: 8rem;
    font-size: 18px;
    margin-top:30px;
    
}


.s--right{
    display: flex;
    flex:1;
    gap:2rem;

   
}

