.works{

    display: flex;
    flex:1;
    height:70vh;
    margin-top:4rem;
    padding: 0 3rem 0 3rem;
    
}


.w--right{
    position: relative; 
    z-index: -9;
   
}

.w--cirwhite
{
    left:1rem;
    top:-10rem;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    background: white;
    position:relative;
    border:1px solid  rgb(219, 216, 216);
    z-index: -1;

   
}

.blur--gray{
    background: #c7c8c9;
    top: 10rem;
    width: 22rem;
    height: 10rem;
    filter:blur(78px);
    left: -8rem;
    z-index: -9;
    position: relative;
    
  }

.w--white>*{
    position:absolute;
}

.s--circle
{
    width: 5rem;
    height: 5rem;
    border-radius: 100%;
    background: white;
    position:absolute;
    border:4px solid  rgb(219, 216, 216);
    z-index: 1;
    align-items: center;
}

.w--cirwhite>:nth-child(1)
{
    top:-3em;
    left:6rem;
   

}
.w--cirwhite>:nth-child(2)
{

    top:6rem;
    left:-3rem;
    

}
.w--cirwhite>:nth-child(3)
{
    
    top:6rem;
    left:6rem;
   

}
.w--cirwhite>:nth-child(4)
{

    top:15rem;
    left:6rem;
}
.w--cirwhite>:nth-child(5)
{
    
    top:6rem;
    left:15rem;
}

.bg--circle {
    width: 13rem;
    height: 13rem;
    border-radius: 100%;
    position: absolute;
    z-index: -2;
}

.blue--cir
{
    background: rgb(42, 71, 236);
    left:8rem;
    top:0rem;
}
.orange--cir
{
    background: rgb(253, 174, 15);
    left:8rem;
    top:5rem;
}
