.intro {
    display:flex;
    height: 77vh;
    margin-top:6rem;
    padding:4rem;
}

.i--left {
    display: flex;
    position: relative;
    flex:1;
    flex-direction: column;
    gap:3rem;

}

.i--name{
    display: flex;
    flex-direction: column;
}
.i--name>:nth-child(1){
    font-size: 4rem;
    color: var(--black);
    font-weight: bold;

}

.i--name>:nth-child(2){
    font-size: 3rem;
    font-weight: bold;
    color:var(--orange);
}

.i--name>:nth-child(3){
    font-size: 16px;
    color: var(--gray);
    margin-top: 10px;
    margin-right:15px;
}


.i--icons{

    display: flex;
    gap: 4.5rem;
    padding-left:2rem;
    color:var(--orange);
    
}
.i--icons>*{
    transform: scale(2.5);
    cursor: pointer;
}

.i--icons>*:hover{
    color: var(--gray);
}

.i--right{
    flex:1;
    position: relative;
  
  }
.i--right>*{
    position: absolute;
    z-index:1;

}

.i--right>:nth-child(1)
{
    transform: scale(0.65);
    left: -15%;
    top:-9%;
}

.i--right>:nth-child(2)
{
    transform: scale(0.60);
    top:4.6rem;
    left: -10%;
    top: -15%;

}

.i--right>:nth-child(3)
{
    transform: scale(1.3);
    left:20%;
    top:-3%;
}
.coolup--imo
{
    transform: scale(0.5);
    top:-4rem;
    left:-5rem;
}

.btn--uni{
    width: 6rem;
    height: 2.5rem;
}
