.floatingdiv{
    justify-content: space-around;
    display:flex;
    background-color: white;
    box-shadow: var(--boxShadow);
    border-radius: 15px;
    align-items:center;
    padding:10px 28px 10px 0px;
    height: 3rem;


}

.floatingdiv>img{
    transform: scale(0.4);
}

.floatingdiv>span{
    font-family: sans-serif;
    font-size: 15px;
    color: var(--gray);
}

.web--float{
    transform: translate(170%, -5%);
   
}
.best--float{
    transform: translate(-10%, 420%);
   
}